.cardTodo {
    cursor: pointer;
    margin-bottom: 10px;
    padding: 12px 20px;
    border: 2px solid var(--c-gray);
    border-radius: 5px;
    background: var(--c-light);
    color: var(--c-base);
    text-align: left;
}

.isAstive {
    color: var(--c-gray);
    background: transparent;
}

.cardTodoHeader {
    display: flex;
    align-items: center;
    gap: 5px;
}

.cardTodoTitle {
    flex-grow: 1;
}

.cardTodoIcon {
    display: inline-block;
    flex: 0 0 auto;
    margin-right: 10px;
    color: var(--c-success);
}

.cardTodoActions {
    display: flex;
    align-items: center;
    gap: 10px;
}
