button[disabled] {
    cursor: default;
}

.btn {
    cursor: pointer;
    height: 45px;
    padding: 5px 15px;
    border: none;
    border-radius: 5px;
    background: var(--c-primary);
    font-size: 20px;
    text-align: center;
}

.btn svg {
    transition: transform 300ms ease;
}

.btn:hover:not([disabled]) {
    background-color: var(--c-brigthen-primary);
}

.btn:hover:not([disabled]) svg {
    transform: scale(1.35);
}

.btn-complete:hover:not([disabled]) {
    color: var(--c-success);
}

.btn-delete:hover:not([disabled]) {
    color: var(--c-danger);
}
