:root {
    --c-primary: #f2f2d7;
    --c-brigthen-primary: #f4f4bc;
    --c-base: #122841;
    --c-light: #f8fdf5;
    --c-gray: #474b4c;
    --c-success: teal;
    --c-danger: darkred;

    --bs-bg: #122841;
    --fz-body: 20px;
}

body {
    min-width: 320px;
    padding: 30px;
    background-color: var(--bs-bg);
    color: var(--c-light);
    font-size: var(--fz-body);
}

h1,
h2 {
    color: var(--c-light);
}

h1 {
    margin: 0 0 1em;
}

h2 {
    margin: 1em;
}

.App {
    text-align: center;
}
